@import "normalize.css";

*,
*::before,
*::after {
	box-sizing: border-box; }
input::-ms-clear {
	display: none; }

input::-ms-reveal {
	display: none; }

.clearfix::after,
.header::after {
    content: "";
    display: table;
    clear: both; }
