.phone-namber {
	font-size: 1.1rem;
	margin-right: 2.8rem;
	color: #fff;
	text-decoration: none;
	padding-left: 1rem;
	position: relative;
	&:hover {
		color: #d53a3a;
		transition: .3s; }
	&:before {
		content: "\f098";
		font-family: "icomoon";
		position: absolute;
		left: -3px;
		&:after {
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			left: 2rem;
			background-color: #d53a3a; } } }
.phone-namber:hover {
	&:after {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0rem; } }
.email-address {
	font-size: 1.1rem;
	margin-right: 1.8rem;
	color: #fff;
	text-decoration: none;
	position: relative;
	&:hover {
		color: #d53a3a;
		transition: .3s; }
	&:before {
		content: "\f0e0";
		font-family: "icomoon";
		position: absolute;
		left: -22px;
		&:after {
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			left: 2rem;
			background-color: #d53a3a;
			filter: blur(90px); } } }
.email-address:hover {
	&:after {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0rem; } }
.logo {
	&:hover {
		&:after {
			content: "";
			height: 2.6rem;
			width: 0.9rem;
			filter: blur(2px);
			background-color: #fff;
			display: block;
			transform: rotate(45deg);
			position: absolute;
			left: -1rem;
			top: 1.4rem;
			animation-name: blick_hover;
			animation-duration: .8s;
			animation-iteration-count: 1; } }
	&:before {
		content: "";
		height: 2.6rem;
		width: 0.9rem;
		filter: blur(2px);
		background-color: #fff;
		display: block;
		transform: rotate(45deg);
		position: absolute;
		left: 0px;
		top: 1.7rem;
		animation-name: blick;
		animation-duration: 12s;
		z-index: 1;
		animation-iteration-count: infinite; } }
@keyframes blick {
	0% {
		left: 0; }
	6% {
		left: 130px; }
	100% {
		left: 130px; } }
@keyframes blick_hover {
	0% {
		left: 0; }
	100% {
		left: 130px; } }

.header-btn {
