.slider {
	position: relative;
	box-shadow: 2px 4px 12px -1px #000; }
.slider__inner  .slider__img {
	width: calc(100vw);
	height: 80vh;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	margin: -5px 0; }
.slider__inner {
	position: relative; }
.slick-list {
	overflow: hidden; }
.slider__inner .slider__item {
	position: relative; }
.slick-track {
	display: flex; }
.slider__inner .slick-arrow {
	position: absolute;
	outline: 0;
	box-sizing: padding-box;
	height: 2.5rem;
	width: 2.5rem;
	cursor: pointer;
	border: 1px solid transparent;
	&:hover {
		box-shadow: 0 0 70px 10px rgba(255, 64, 64, 1); } }
.slider__inner .slick-prev {
	position: absolute;
	left: 1.9rem;
	top: 50%;
	transform: translateY(-50%);
	z-index: 10000;
	background-color: rgba(255, 64, 64, .6);
	&:hover {
		background-color: #f02323; }

	&:before {
		content: "\f060";
		font-family: 'icomoon';
		font-size: 1.4rem;
		position: absolute;
		bottom: .45rem;
		right: 0.55rem; } }
.slider__inner .slick-prev:after {
	position: absolute;
	top: -10rem;
	left: -1.99rem;
	bottom: -10rem;
	right: -1.99rem;
	content: ""; }
.slider__inner .slick-next {
	position: absolute;
	right: 1.9rem;
	top: 50%;
	transform: translateY(-50%);
	z-index: 10000;
	background-color: rgba(255, 64, 64, .6);
	&:hover {
		background-color: #f02323; }
	&:before {
		content: "\f061";
		font-family: "icomoon";
		font-size: 1.4rem;
		position: absolute;
		bottom: .45rem;
		right: 0.55rem; } }
.slider__inner .slick-next:after {
	top: -10rem;
	left: -1.99rem;
	bottom: -10rem;
	right: -1.99rem;
	content: "";
	position: absolute; }
.slider__inner .slick-dots {
	display: flex;
	position: absolute;
	justify-content: center;
	width: 100%;
	position: absolute;
	left: 0;
	bottom: 1rem;
	right: 0;
	li {
		margin-left: 1rem;
		&:first-child {
			margin-left: 0; }
		button {
			font-size: 0;
			height: .75rem;
			width: .6rem;
			border-radius: 50%;
			background-color: #ccc;
			border: none;
			outline: none;
			cursor: pointer; } }
	li.slick-active {
		button {
			width: 1rem;
			height: 1rem; } } }
.slider__inner .slick-disabled.slick-arrow {
	background: transparent;
	cursor: default;
	&:hover {
		box-shadow: none; }
	&:before {
		font-size: 0; } }
.slider__inner .slider__paragraph {
	padding: 0;
	margin: 0;
	left: 0;
	right: 0;
	bottom: 3.6rem;
	text-align: center;
	font-size: 2rem;
	position: absolute {
 	margin: 0 auto; } }
.slider__inner .slider__block {
	width: 100%;
	height: 8rem;
	background-color: rgba(0,0,0,.6);
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	margin: -5px 0; }
.slider__inner .slider__paragraph-text {
	color: #1febf2;
	color: transparent;
	-webkit-text-stroke: 1px #1febf2;
	font-size: 50px;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	cursor: pointer;
	position: relative;
	&:hover {
			background: #1febf2;
			-webkit-background-clip: text;
			transition: .5s;
			border-radius: 10px;
			z-index: 4;
			&::before {
				content: "";
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				position: absolute;
				z-index: 4;
				background: linear-gradient(90deg, #1febf2, #1efda1);
				filter: blur(50px); } } }




